@import 'variables';

.main {
    &__catalog {
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &__block1 {
            background:  url("../img/catalog_photo.png") no-repeat center;
            @include photo_block;
            &__h2 {
                @include photo_block_h2;
                top: 150px; } }
        &__block2 {
            position: relative;
            display: flex;
            flex-direction: column;
            padding: 30px 0px;
            justify-content: space-between;
            align-items: center;
            max-width: calc( 100vw - 300px );
            min-width: calc( 100vw - 300px);
            &__underline {
                border: none;
                width: 50%;
                border-bottom: 1px solid #27272738; }
            &__category {
                min-height: 600px;
                margin: 40px 0px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                &__name {
                    align-self: flex-start;
                    text-align: start;
                    font-family: 'main';
                    font-weight: 700;
                    &__theme {
                        font-size: 30px;
                        margin-bottom: 25px;
                        &__underline {
                            width: 25%;
                            border: none;
                            border-top: 3px solid $main-color; } }
                    &__title {
                        font-size: 40px;
                        text-transform: uppercase;
                        margin-bottom: 10px; }
                    &__text {
                        font-size: 20px;
                        font-weight: 300;
                        color: rgba(0, 0, 0, 0.70); } }
                &__button {
                    @include button; } } } } }

